import React from 'react'
import ShopItem from './ShopItem'



class ShopList extends React.Component {
	render() {
		let shops = this.props.shops;
		let selectShop = this.props.selectShop;
		let nextPage = this.props.nextPage;
		let toggleMode = this.props.toggleMode;
		
		return (
			<div>
				<div className="container-fluid" style={{padding: '0', marginTop: '22px'}}>
					<div className="row">	
						{shops.map((shop, index) => (
							<ul key={index} className="col-12 col-md-4 list-unstyled" style={{marginTop: '18px'}}>
								<ShopItem key={index} onClick={() => {selectShop(index); nextPage();}} content={shop} />
							</ul>
						))}
					</div>
				</div>

				<span onClick={() => toggleMode()}><img src="/static/left-on.svg" className="position-fixed" style={{width: '90px', right: '12px', bottom: '9px'}}/></span>
			</div>
		)
	}
}
